import React, { Component, Fragment } from 'react';
import { PieChart, Pie, Cell } from 'recharts';
import { getStudents } from 'services/chart';
import { Tag } from 'antd';
import { roundUpDouble } from 'utils/formatUtil';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <Fragment>
      {percent !== 0 ? (
        <text
          x={x}
          y={y}
          fill="white"
          textAnchor={x > cx ? 'start' : 'end'}
          dominantBaseline="central"
        >
          {`${roundUpDouble(percent * 100, 1)}%`}
        </text>
      ) : null}
    </Fragment>
  );
};

export default class Example extends Component {
  state = {
    students: [],
  };

  componentDidMount = async () => {
    const res = await getStudents();
    this.setState({
      students: res.data.pieces.map(item => {
        item.name = item.label;
        return item;
      }),
    });
  };

  render() {
    return (
      <div>
        <PieChart width={400} height={300}>
          <Pie
            data={this.state.students}
            cx={250}
            cy={150}
            labelLine={false}
            label={renderCustomizedLabel}
            outerRadius={100}
            fill="#8884d8"
            dataKey="value"
          >
            {this.state.students.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>
        <div style={{ textAlign: 'center' }}>
          {this.state.students.map((entry, index) => (
            <Tag key={`cell-${index}`} color={COLORS[index % COLORS.length]}>
              {entry.name} : {entry.value}
            </Tag>
          ))}
        </div>
      </div>
    );
  }
}



