import React, { Component, Fragment } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { getChartSummaryAdmission } from 'services/chart';
import { Tag } from 'antd';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];
const data = [
  {
    name: 'Page A',
    uv: 4000,
    pv: 2400,
    amt: 2400,
  },
  {
    name: 'Page B',
    uv: 3000,
    pv: 1398,
    amt: 2210,
  },
  {
    name: 'Page C',
    uv: 2000,
    pv: 9800,
    amt: 2290,
  },
  {
    name: 'Page D',
    uv: 2780,
    pv: 3908,
    amt: 2000,
  },
  {
    name: 'Page E',
    uv: 1890,
    pv: 4800,
    amt: 2181,
  },
  {
    name: 'Page F',
    uv: 2390,
    pv: 3800,
    amt: 2500,
  },
  {
    name: 'Page G',
    uv: 3490,
    pv: 4300,
    amt: 2100,
  },
];

export default class EnrollmentStackedColumnChart extends Component {
  state = {
    enrollmentChartData: [],
  };

  componentDidMount = async () => {
    const res = await getChartSummaryAdmission();
    this.setState({
      enrollmentChartData: res.data.columns.map(item => {
        const value = {
          name: item.xValue,
        };
        item.stackeds.forEach(element => {
          value[element.label] = element.value;
        });
        return value;
      }),
    });
  };

  render() {
    console.log(this.state.enrollmentChartData);
    return (
      <Fragment>
        <BarChart
          width={500}
          height={300}
          data={this.state.enrollmentChartData}
          margin={{
            top: 20,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          {Object.keys(this.state.enrollmentChartData[0] || {}).map((key, index) => {
            if (key === 'name') return null;
            return <Bar key={`Bar-${index}`} dataKey={key} stackId="a" fill={COLORS[index % 4]} />;
          })}
        </BarChart>
        {/* <div style={{ textAlign: 'center' }}>
          {Object.entries(this.state.enrollmentChartData[0] || {}).map(([key, value], index) => {
            if (key === 'name') return null;
            return (
              <Tag key={`cell-${index}`} color={COLORS[index % COLORS.length]}>
                {key}
              </Tag>
            );
          })}
        </div> */}
      </Fragment>
    );
  }
}



