import React from 'react';
import StudentPieChart from '../StudentPieChart';
import EnrollmentStackedColumnChart from '../EnrollmentStackedColumnChart';

class DashboardBeta extends React.Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-12 col-xl-6">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Sinh viên</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <StudentPieChart />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-12 col-xl-6">
            <div className="card">
              <div className="card-header">
                <div className="utils__title">
                  <strong>Tuyển sinh</strong>
                </div>
                <div className="utils__titleDescription" />
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-xl-12">
                    <EnrollmentStackedColumnChart />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DashboardBeta;



